import React from 'react';
import {Button, InputGroup, Intent} from '@blueprintjs/core';
import PropTypes from 'prop-types';
import {connect} from 'react-redux';
import {bindActionCreators} from 'redux';
import {withRouter} from 'react-router-dom';
import {isFormValid} from "../../../../helpers";
import {addService, loadServices} from '../../../../actions/MachineServicesActions';
import Toaster from "../../../Common/Toaster";
import {ADD_MACHINE_SERVICE} from "../../../../helpers/constants";
import _isEqual from "lodash/isEqual";

class New extends React.Component{

    state = {
        name: '',
        display: '',
        path: '',
    };

    componentDidUpdate(prevProps){
        const {OPERATION_SUCCESSFUL: OLD_OPERATION_SUCCESSFUL, OPERATION_FAILED: OLD_OPERATION_FAILED} = prevProps;
        const {OPERATION_SUCCESSFUL, OPERATION_FAILED, history, authUser: {token}, loadServices} = this.props;

        const service = this.state.name.toUpperCase();

        if(!_isEqual(OLD_OPERATION_SUCCESSFUL, OPERATION_SUCCESSFUL)){
            loadServices(token, {});

            if(OPERATION_SUCCESSFUL.action === ADD_MACHINE_SERVICE){
                history.goBack();
                Toaster.show({
                    message: `${service} Successfully Added 😃`,
                    intent: Intent.SUCCESS,
                    icon: 'tick' });
            }
        }

        if(!_isEqual(OLD_OPERATION_FAILED, OPERATION_FAILED)){
            if(OPERATION_FAILED.action === ADD_MACHINE_SERVICE){
                console.log(OPERATION_FAILED.data);
                Toaster.show({
                    message: `Could Not Add ${service} 😞`,
                    intent: Intent.DANGER,
                    icon: 'error'
                });
            }
        }
    }

    _handleInputChange = (name, value) => {
        this.setState({[name]: value});
    };

    _handleAdd = () => {
        const {authUser: {token}, addService} = this.props;
        const {name, display, path} = this.state;

        let data = new FormData();

        data.append('name', name);
        data.append('display', display);
        data.append('path', path);

        addService(token, data);
    };

    _isFormValid = () => {
        const {name, display, path} = this.state;

        let required = {name, display, path};

        return isFormValid(required);
    }

    render(){
        const {addingService} = this.props;
        const {name, display, path} = this.state;
        const disableSaveButton = !this._isFormValid();

        return [
            <div key="body" className="bp3-dialog-body">
                <label className="bp3-label">
                    Name
                    <input
                        value={name}
                        name={'name'} onChange={({target: {name, value}}) => this._handleInputChange(name, value)}
                        className="bp3-input bp3-fill" type="text" dir="auto" />
                </label>
                <label className="bp3-label">
                    Display Name
                    <input
                        value={display}
                        name={'display'} onChange={({target: {name, value}}) => this._handleInputChange(name, value)}
                        className="bp3-input bp3-fill" type="text" dir="auto" />
                </label>
                <label className="bp3-label">
                    Full Path
                    <input
                        value={path}
                        name={'path'} onChange={({target: {name, value}}) => this._handleInputChange(name, value)}
                        className="bp3-input bp3-fill" type="text" dir="auto" />
                </label>
            </div>,
            <div key="footer" className="bp3-dialog-footer">
                <div className="bp3-dialog-footer-actions">
                    <Button
                        disabled={disableSaveButton}
                        loading={addingService}
                        intent={Intent.PRIMARY}
                        onClick={this._handleAdd}
                        icon="tick"
                        text="save"
                    />
                </div>
            </div>
        ];
    }

    static propTypes = {
        history: PropTypes.object.isRequired,
        authUser: PropTypes.object.isRequired,
        OPERATION_SUCCESSFUL: PropTypes.object.isRequired,
        OPERATION_FAILED: PropTypes.object.isRequired,
        addService: PropTypes.func.isRequired,
        loadServices: PropTypes.func.isRequired,
        addingService: PropTypes.bool.isRequired,
        services: PropTypes.array.isRequired,
    };
}

const mapStateToProps = (
    {OPERATION_SUCCESSFUL, OPERATION_FAILED, authUser, addingService, services}) => (
        {OPERATION_SUCCESSFUL, OPERATION_FAILED, authUser, addingService, services});

const mapDispatchToProps = dispatch => bindActionCreators({addService, loadServices}, dispatch);

export default withRouter(connect(mapStateToProps, mapDispatchToProps)(New));


